.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


*, *:before, *:after {
  box-sizing: border-box;
}
html {
  font-size: 100%;
}
html, body {
  min-height: 100%;
  min-height: 100vh;
}
.ce-block__content {
  max-width: 80%;
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
}
.image-tool--withBackground .image-tool__image {
  background-color: #e9ebee !important;
}

body {
  background-color: #e9ebee !important;

}
.ce-header {
  padding: 0.5em 0 !important;
  margin-bottom: 0em !important;

}

.cdx-simple-image .cdx-input {
display: none !important;
}
.image-tool__image {
  margin-bottom: 0px !important;
}
.cdx-input {
  display: none !important;
}
#editor-js {
  background-color: white;
  margin-left: auto;
  margin-right: auto;

}
.ce-toolbar__content {
  max-width: 80%;
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
}

.cdx-simple-image img {
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
}

.navbar-light .navbar-brand, .navbar-light .navbar-brand:focus, .navbar-light {
  color: black !important;
}
.navbar-light .navbar-nav .nav-link {
  color: black !important;
}

.navbar-light:hover .navbar-nav:hover .nav-link:hover {
  color: #a8a8a8 !important;
}

.scroll {
  width: 40px;
  height: 40px;
  position: fixed;

  bottom: 10px;
  right: 10px;
  border-radius: 5px;
  border: none;
  z-index: 2;
}

.app {
  max-width: 60rem;
  width: 100%;
  background: lightgray;
  padding: 1rem;
  font-size: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  min-height: 100%;
  min-height: 100vh;
  margin: 0 auto;
}
.app > header, .app > main, .app > footer {
  width: 100%;
  padding: 1rem;
  margin: 0.5rem 1rem;
  text-align: center;
  transition: 200ms;
}
.app > header:hover, .app > main:hover, .app > footer:hover {
  box-shadow: 0 0 2rem gray;
}
.app > header {
  display: block;
  color: white;
  background: gray;
  text-transform: uppercase;
}
.app > main {
  flex: 1 1 auto;
  color: white;
  background: #333;
}
.app > footer {
  background: white;
}

.navbar-inverse .brand, .navbar-inverse .nav > li > a
.navbar .brand, .navbar .nav > li > a:hover {
  color: #F56E6E;
}

   /* Set height of the grid so .sidenav can be 100% (adjust if needed) */
 .row.content {height: 1500px}

/* Set gray background color and 100% height */
.sidenav {
  background-color: #dcdcdc;
  height: 100%;
}
.codex-editor {
  padding: 0 0 30px 0 !important;
}

.image-tool {
  --bg-color: #e6e6e6 !important;
}
.cdx-block {
  padding: .0em 0 !important;
}
/* Set gray background color and 100% height */
.pageContent {
  background-color: #dcdcdc;
  height: 100%;
}
.cdx-input {
  border: 0px !important;
  -webkit-box-shadow: none !important;
}

.myNav {
}
.myNav:hover, .myNav:focus, .myNav:active, .myNav.active {
  color: #000000;
  background-color: #dcdcdc;
  border-color: rgba(0, 0, 0, 0.1); /*set the color you want here*/
}

.myAct:hover, .myAct:focus, .myAct:active, .myAct.active {
  color: #000000;
  border-color: rgba(0, 0, 0, 0.1); /*set the color you want here*/
}

.cdx-quote__text {
  min-height: auto !important;
}

.myClickableText {
}
.myClickableText:hover, .myClickableText:focus, .myClickableText:active, .myClickableText.active {
  color: #000000;
  background-color: #dcdcdc;
  border-color: rgba(0, 0, 0, 0.1); /*set the color you want here*/
}


.myHeader {
  background: rgb(255,255,255);
  background: linear-gradient(0deg, rgba(255,255,255,1) 30%, rgba(91,192,222,1) 100%);
}

/* Set black background color, white text and some padding */
footer {
  background-color: #555;
  color: white;
  padding: 15px;
}

/* On small screens, set height to 'auto' for sidenav and grid */
@media screen and (max-width: 767px) {
  .sidenav {
    height: auto;
    padding: 15px;
  }
  .row.content {height: auto;}
}

